#login-page {
    position: relative;
    z-index: 3;
    height: 100%;
    font-size: $font-small;
    @include mobile {
        font-size: $font-medium;
        display: flex;
        align-items: flex-start;
        height: 60%
    }
    @include flex-layout();
    .login-wrap {
        width: 500px;
        padding: 2rem 3rem;
        border-radius: 5px;
        background-color: rgba(0, 0, 0, 0.6);
        * {
            display: block;
            margin: 1rem 0;
        }
        label:nth-of-type(1),
        label:nth-of-type(2) {
            @include floation-label;
        }
        .error {
            color: $main-color;
            font-size: $font-small;
        }
        button:nth-of-type(1) {
            @include btn-style();
            width: 100%;
            @include mobile {
                font-size: $font-medium;
            }
        }
        .or {
            text-align: center;
        }
        button:nth-of-type(2) {
            @include btn-style(#666);
            width: 100%;
            @include mobile {
                font-size: $font-medium;
            }
        }
        a {
            text-align: center;
            text-decoration: underline;
        }
        label:nth-of-type(3) {
            display: flex;
            gap: 10px;
        }
        .sign-up {
            // text-align: center;
            a {
                text-align: left;
            }
            strong {
                display: inline-block;
            }
        }
    }
}
