/* Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
a {
    text-decoration: none;
    color: inherit;
}
li {
    list-style: none;
}
html {
    font-size: 16px;
    @include tablet {
        font-size: 14px;
    }
    @include mobile {
        font-size: 10px;
    }
}
body {
    background-color: $bg-color;
    color: $text-color;
    word-break: keep-all;
}
